* {
  padding: 0;
  margin: 0;
}

body {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(89, 179, 143);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

h1 {
  text-align: center;
  margin-bottom: 1rem;
}

.container {
  height: 700px;
  width: 700px;
  border-radius: 5px;
  margin-bottom: 1rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  gap: 1rem;
}

.card{
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  transform: rotateY(180deg);
  animation: hideCard 2s linear;
  transition: transform 0.5s;
}

.card.active {
  transform: rotateY(0deg);
}

.card.correct {
  background-color: #65e469;
}

.card.wrong {
  background-color: #fd245a;
}

.card img {
  max-width: 70%;
  max-height: 70%;
  transform: scale(0);
  animation: hideImage 2s linear;
  transition: transform 0.5s;
}

.card.active img {
  transform: scale(1);
}


@keyframes hideCard {
  0%, 70% {
    transform: rotateY(0);
  }  
  100% {
    transform: rotateY(180deg);
  }
}

@keyframes hideImage {
  0%, 70% {
    transform: scale(1);
  }  
  100% {
    transform: scale(0);
  }
}
